<template>
  <div>
    <div class="sec_header">{{title}}</div>
    <div class="editor">
      <div ref="toolbar" class="toolbar"></div>
      <div ref="editor" class="text"></div>
    </div>
  </div>
</template>
<script>
import E from "wangeditor";
export default {
  name: "editoritem",
  data() {
    return {
      // uploadPath,
      editor: null,
      info_: null
    };
  },
  model: {
    prop: "value",
    event: "change"
  },
  props: {
    title: String,
    value: {
      type: String,
      default: ""
    },
    isClear: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    isClear(val) {
      // 
      if (val) {
        this.editor.txt.clear();
        this.info_ = null;
      }
    },
    value: function(value) {
      if (value !== this.editor.txt.html()) {
        this.editor.txt.html(this.value);
      }
    }
    //valuevalue
  },
  mounted() {
    this.seteditor();
    this.editor.txt.html(this.value);
  },
  methods: {
    seteditor() {
      // http://192.168.2.125:8080/admin/storage/create
      this.editor = new E(this.$refs.toolbar, this.$refs.editor);
      this.editor.customConfig.uploadImgShowBase64 = true; // base 64 
      // this.editor.customConfig.uploadImgServer =
      //   "http://otp.cdinfotech.top/file/upload_images"; // 
      // this.editor.customConfig.uploadImgHeaders = {}; //  header
      // this.editor.customConfig.uploadFileName = "file"; // 
      this.editor.customConfig.uploadImgMaxSize = 2 * 1024 * 1024; //  2M
      this.editor.customConfig.uploadImgMaxLength = 6; //  3 
      // this.editor.customConfig.uploadImgTimeout = 3 * 60 * 1000; // 

      // 
      this.editor.customConfig.menus = [
        "head", // 
        "bold", // 
        "fontSize", // 
        "fontName", // 
        "italic", // 
        "underline", // 
        "strikeThrough", // 
        "foreColor", // 
        "backColor", // 
        "link", // 
        "list", // 
        "justify", // 
        "quote", // 
        "emoticon", // 
        "image", // 
        "table", // 
        "video", // 
        "code", // 
        "undo", // 
        "redo", // 
        "fullscreen" // 
      ];

      // this.editor.customConfig.uploadImgHooks = {
      //   fail: (xhr, editor, result) => {
      //     // 
      //   },
      //   success: (xhr, editor, result) => {
      //     // 
      //   },
      //   timeout: (xhr, editor) => {
      //     // 
      //   },
      //   error: (xhr, editor) => {
      //     // 
      //   },
      //   customInsert: (insertImg, result, editor) => {
      //     // 
      //     //resultdata[{url:""},...]
      //     // console.log(result.data[0].url)
      //     //insertImg()
      //     //
      //     // for (let i = 0; i < 1; i++) {
      //     // console.log(result)
      //     let url = "http://otp.cdinfotech.top" + result.url;
      //     insertImg(url);
      //     // }
      //   }
      // };
      this.editor.customConfig.onchange = html => {
        this.info_ = html; // 
        this.$emit("change", this.info_); // 
      };
      // 
      this.editor.create();
    }
  }
};
</script>
<style lang="css">
.editor {
  width: 100%;
  margin: 0 auto;
  position: relative;
  z-index: 0;
}
/*editor css*/
.toolbar {
  border: 1px solid #ccc;
}

.text {
  border: 1px solid #ccc;
  min-height: 200px;
  max-height: 400px;
  overflow: auto;
}

/*editor css*/
</style>