<template>
  <div class="container my-set pt-3 px-5">
    <div class="row">
      <div class="col-md-12 order-md-1">
        <div class="col-6 offset-3">
          <div class="input-group mb-3">
            <div class="input-group-prepend ml-3">
              <span class="input-group-text"></span>
            </div>
            <input
              v-model="userName"
              type="text"
              class="form-control"
              placeholder=""
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
            />
            <div class="input-group-append ml-2">
              <button
                @click="toPage(1)"
                class="btn btn-outline-secondary"
                type="button"
                id="button-addon2"
              >Go</button>
            </div>
          </div>
        </div>
        <MyTabel :pageInfo="pageInfo" @toPage="toPage">
          <thead class="thead-light" slot="thead">
            <tr>
              <th scope="col">Rank</th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody slot="tbody">
            <tr v-for="(item,index) in userList" :key="item.userId">
              <th scope="row">{{index+1}}</th>
              <td>
                <router-link :to="'/user/info/' + item.userId">{{item.userName}}</router-link>
              </td>
              <td>{{item.solved}}</td>
              <td>{{item.attempted}}</td>
              <td>{{item.accepted}}</td>
              <td>{{item.submissions}}</td>
            </tr>
          </tbody>
        </MyTabel>
      </div>
    </div>
  </div>
</template>

<script>
import request from "../../../api/ajax.js";
import MyTabel from "../../../components/Table/MyTable.vue";
export default {
  data() {
    return {
      userName: null,
      userList: [],
      pageInfo: {}
    };
  },
  methods: {
    toPage(index) {
      // console.log("topage");
      this.elderProblemId = this.problemId;
      this.elderTitle = this.title;
      request({
        url: "/user/set/" + index,
        params: {
          userName: this.userName
        }
      })
        .then(res => {
          // console.log(res);
          // console.log(result.extend.pageInfo.list)
          this.userList = res.data.extend.pageInfo.list;
          this.pageInfo = res.data.extend.pageInfo;
          // console.log(vue.pageInfo)
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  components: {
    MyTabel
  },
  created() {
    this.toPage(1);
  },
  updated() {
    // $('[data-toggle="tooltip"]').tooltip();
  }
};
</script>

