<template>
  <div class="container my-set pt-3 px-5">
    <div class="row">
      <div class="col-md-12 order-md-1">
        <router-link to="/problem/add"></router-link>
        <div class="col-10 offset-1">
          <div class="input-group mb-3">
            <div class="input-group-prepend">
              <span class="input-group-text">PID</span>
            </div>
            <input
              v-model="problemId"
              type="text"
              class="form-control"
              placeholder="PID"
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
            />
            <div class="input-group-prepend ml-3">
              <span class="input-group-text"></span>
            </div>
            <input
              v-model="title"
              type="text"
              class="form-control"
              placeholder=""
              aria-label="Recipient's username"
              aria-describedby="button-addon2"
            />
            <div class="input-group-append ml-2">
              <button @click="toPage(1)" class="btn btn-primary" type="button" id="button-addon2">
                <!-- <span class="fa fa-search fa-lg text-secondary"></span> -->
                Go
              </button>
            </div>
          </div>
        </div>
        <MyTabel :pageInfo="pageInfo" @toPage="toPage">
          <thead class="thead-light" slot="thead">
            <tr>
              <th scope="col">PID</th>
              <th scope="col"></th>
              <th scope="col"></th>
              <!-- <th scope="col">
                <span class="fa fa-tags fa-lg text-secondary"></span>
                
              </th>-->
              <th scope="col"></th>
              <th scope="col">AC%</th>
            </tr>
          </thead>
          <tbody slot="tbody">
            <tr v-for="item in problemList" :key="item.problemId">
              <th scope="row">{{item.problemId}}</th>
              <td>
                <span
                  v-if="item.userSolved"
                  data-toggle="tooltip"
                  data-placement="top"
                  title=""
                  class="fa fa-check fa-lg text-success"
                ></span>
                <span
                  v-else-if="item.userSubmitted"
                  data-toggle="tooltip"
                  data-placement="top"
                  title=""
                  class="fa fa-close fa-lg text-danger"
                ></span>
                <span
                  v-else
                  data-toggle="tooltip"
                  data-placement="top"
                  title=""
                  class="fa fa-circle-o fa-lg text-warning"
                ></span>
              </td>
              <td>
                <router-link :to="'/problem/view/' + item.problemId">{{item.title}}</router-link>
              </td>
              <!-- <td></td> -->
              <td>{{item.submissions}}</td>
              <td>{{item.submissions ? Math.round(item.accepted / item.submissions * 100) + '%' : ''}}</td>
            </tr>
          </tbody>
        </MyTabel>
      </div>
    </div>
  </div>
</template>

<script>
import request from "../../api/ajax.js";
import $ from "jquery";
import MyTabel from "../../components/Table/MyTable.vue";
export default {
  data() {
    return {
      problemId: null,
      title: null,
      problemList: [],
      pageInfo: {}
    };
  },
  methods: {
    toPage(index) {
      // console.log("topage"+index);
      this.elderProblemId = this.problemId;
      this.elderTitle = this.title;
      request({
        url: "/problem/getProblemSet/" + index,
        params: {
          problemId: this.problemId,
          title: this.title
        }
      })
        .then(res => {
          // console.log(res);
          // console.log(result.extend.pageInfo.list)
          this.problemList = res.data.extend.pageInfo.list;
          this.pageInfo = res.data.extend.pageInfo;
          // console.log(vue.pageInfo)
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  components: {
    MyTabel
  },
  created() {
    this.toPage(1);
  },
  updated() {
    $('[data-toggle="tooltip"]').tooltip();
    // console.log('update')
  }
};
</script>

