<template>
  <div class="container" style="margin-top: 80px;">
    <div class="row">
      <div class="col-md-12 order-md-1">
        <div class="problem" style="display:block;">
          <div class="input-group mb-3 col-md-6 offset-md-3">
            <div class="input-group-prepend">
              <span class="input-group-text"></span>
            </div>
            <input
              name="title"
              type="text"
              v-model="title"
              :class="{'form-control': true,'is-invalid': title == '','is-valid': title != ''}"
              placeholder=""
              aria-label="Username"
              aria-describedby="basic-addon1"
            />
            <div class="invalid-feedback">.</div>
          </div>
          <!-- <div class="input-group mb-3 col-md-6 offset-md-3">
            <div class="input-group-prepend">
              <span class="input-group-text"></span>
            </div>
            <input
              name="tag"
              type="text"
              v-model="tag"
              :class="{'form-control': true,'is-invalid': title == '','is-valid': title != ''}"
              placeholder=""
              aria-label="Username"
              aria-describedby="basic-addon1"
            />
            <div class="invalid-feedback">.</div>
          </div> -->
          <div>
            <div class="input-group mb-3 col-md-6 offset-md-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon1">(ms)</span>
              </div>
              <input
                name="timeLimit"
                v-model="timeLimit"
                type="number"
                :class="{'form-control': true,'is-invalid': timeLimit == '','is-valid': timeLimit != ''}"
                placeholder="(ms)"
                aria-label="Username"
                aria-describedby="basic-addon1"
              />
              <div class="invalid-feedback">.</div>
            </div>
            <div class="input-group mb-3 col-md-6 offset-md-3">
              <div class="input-group-prepend">
                <span class="input-group-text" id="basic-addon2">(MB)</span>
              </div>
              <input
                name="memoryLimit"
                v-model="memoryLimit"
                type="number"
                :class="{'form-control': true,'is-invalid': memoryLimit == '','is-valid': memoryLimit != ''}"
                placeholder="(MB)"
                aria-label="Username"
                aria-describedby="basic-addon1"
              />
              <div class="invalid-feedback">.</div>
            </div>
          </div>
          <div class="des">
            <!--  -->
            <TextEditor
              v-model="problem[item.id]"
              :isClear="isClear"
              :title="item.title"
              v-for="item in textEditors"
              :key="item.id"
            ></TextEditor>
            <div class="sec_header">
              
              <button class="btn btn-primary btn-sm" @click="addCase"></button>
            </div>
            <div class="col-12 mb-3">
              <div class="mb-3">10</div>
              <div>
                <judge-case
                  v-model="judgeData[index]"
                  v-for="(item,index) in judgeData"
                  :key="index"
                  :index="index"
                  @toDelCase="toDelCase(index)"
                ></judge-case>
              </div>
            </div>
            <div class style="color: red; text-align: center" hidden="hidden"></div>
            <div style="display: flex; justify-content: center;">
              <button
                @click="addProblem"
                class="btn btn-primary btn-lg justify-content-sm-center"
                type="submit"
              ></button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TextEditor from "../../components/TextEditor/TextEditor.vue";
import JudgeCase from "../../components/JudgeCase/JudgeCase.vue";
import request from "../../api/ajax.js";
export default {
  data() {
    return {
      textEditors: [
        {
          title: "",
          id: "description"
        },
        {
          title: "",
          id: "formatInput"
        },
        {
          title: "",
          id: "formatOutput"
        },
        {
          title: "",
          id: "sampleInput"
        },
        {
          title: "",
          id: "sampleOutput"
        },
        {
          title: "",
          id: "hint"
        }
      ],
      problem: {},
      isClear: false,
      title: "",
      tag: '',
      timeLimit: "",
      memoryLimit: "",
      judgeData: []
    };
  },
  components: {
    TextEditor,
    JudgeCase
  },
  methods: {
    addCase() {
      if(this.judgeData.length >= 10) return
      this.judgeData.push({ in: "", out: "" });
    },
    addProblem() {
      this.problem.judgeData = JSON.stringify(this.judgeData);
      //   console.log(this.problem.judgeData == "[]")
      this.problem.title = this.title;
      this.problem.timeLimit = this.timeLimit;
      this.problem.memoryLimit = this.memoryLimit;
      //   console.log(this.problem.judgeData)
      console.log(this.problem);
      if (
        this.problem.title == "" ||
        this.problem.timeLimit == "" ||
        this.problem.memoryLimit == "" ||
        this.problem.JudgeCase == "[]"
      ) {
        return;
      }
      console.log('in')
      request({
        url: "/problem/add",
        method: "POST",
        data: this.problem
      })
        .then(res => {
          console.log(res);
          if (res.data.success) {
            this.$router.replace("/problem/view/" + res.data.extend.pid)
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    toDelCase(index) {
      this.judgeData.splice(index - 1, 1);
    },
  }
};
</script>
<style>
.problem .des {
  margin: 20px 80px 12px 80px;
  padding: 16px 40px 16px 40px;
  background: #e4f1ff;
}

* {
  margin: 0;
  padding: 0;
  font-family: "", "Times New Roman";
}

.problem .des .sec_header,
.problem .des .pdsec {
  font-family: "", "", "";
  margin: 16px 0px 8px 0px;
  font-size: 16px;
}

.invalid-feedback {
  font-size: 1rem;
}
</style>