<template>
  <img ref="verifiedImg" alt="" @click="getVerifyImage" :src="verifiedImg" />
</template>

<script>
import request from "../../api/ajax.js";
export default {
  data() {
    return {
      verifiedImg: ""
    };
  },
  methods: {
    getVerifyImage() {
      request({
        url: "/verify/image",
        method: "get",
        responseType: "arraybuffer"
      })
        .then(response => {
          //
          return (
            "data:image/png;base64," +
            btoa(
              new Uint8Array(response.data).reduce(
                (data, byte) => data + String.fromCharCode(byte),
                ""
              )
            )
          );
        })
        .then(res => {
          // console.log(res);
          this.verifiedImg = res;
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    this.getVerifyImage();
  }
};
</script>
