/*
mutationstate
 */

import {
  RECEIVE_PROBLEM,
  RECEIVE_USER
} from './mutations-types'
import request from '../api/ajax.js'

export default {
  // 
   getUser({commit}) {
     request({
        url: "/user/currentInfo",
        method: "GET"
      })
      .then(res => {
        // console.log(res);
        if(res.data.success){
          const user = res.data.extend.user;
          commit(RECEIVE_USER, {
            user
          })
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  // 
  async getProblem({commit}, pid) {
    await request({
        url: "/problem/" + pid,
        method: "GET"
      })
      .then(res => {
        // console.log(res);
        const problem = res.data.extend.problem;
        commit(RECEIVE_PROBLEM, {
          problem
        })
      })
      .catch(err => {
        console.log(err);
      });
  },
}